<template>
  <div>
    <vue-weather api-key='305cf867acd95098efcf3daf2c07b38e' units='uk' v-bind:updateInterval='update'/>
  </div>
</template>

<script>
import VueWeather from 'vue-weather-widget'
export default {
  components: {
    VueWeather
  },
  data () {
    return {
      update: 1000
    }
  }
}
</script>
<style scoped>
.vww__widget{
    color: white;
}
</style>



